      text-decoration: none; }
      .footer .footer-modules .module-link a:link, .footer .footer-modules .module-link a:visited {
        color: rgba(0, 0, 0, 0.8);
        text-transform: none;
        text-decoration: none; }
      .footer .footer-modules .module-link a:hover {
        color: black;
        text-transform: none;
        text-decoration: none; }
      .footer .footer-modules .module-link a:active {
        color: black;
        text-transform: none;
        text-decoration: none; }



